import React from 'react';
import {
  BrowserRouter,
  Switch,
  Route,
} from 'react-router-dom';

// Pages
import Login from '../pages/login';
import Register from '../pages/register';
import Home from '../pages';

export default function routes() {
  return (
    <BrowserRouter basename="https://filipe-machado.github.io">
      <Switch>
        <Route path="/eclasse-app/login" exact>
          <Login />
        </Route>

        <Route path="/eclasse-app/register" exact>
          <Register />
        </Route>

        <Route path="/eclasse-app" exact>
          <Home />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}
